* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
ul, ol {
    list-style: none;
}
a {
    text-decoration: none;
}
button {
    outline: 0;
    border: 0;
}
body {
    font-family: roboto,sans-serif;
    line-height: 1.5;
    color: #37383c;
    background-color: #fbfbfb;
}